import React, { Component } from 'react';
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import './TextBox.css';

class TextBox extends Component {

	constructor(props) {

		super(props);
		this.state = 
		{
			text: this.props.resText.response,
			change: 2,
			interval: []
		};
		this.onChange = this.onChange.bind(this);

	}
	
	componentDidMount() {
		
		let interval = setInterval(this.checkSend.bind(this), 5000);
		this.setState({interval: interval})
	
	}

	componentWillUnmount() {

		clearInterval(this.state.interval);
	
	}
	
	checkSend()
	{
			if(this.state.change == 1)
			{
				this.props.autoSend()
				this.setState({change: 2})
			}
			if(this.state.change == 0)
				this.setState({change: 1})
	}

	onChange(value) {
		
		this.setState({ text: value, change: 0 });
		this.props.callbackText(this.state);
	
	}

	render() {

		return (
			<div className="innerTextBox">
			
				 <ReactQuill 
					theme='snow'
					value={this.state.text || ''}
					onChange={this.onChange} 
				/>
				
			</div>
		)

	}

}

export default TextBox;